import React from 'react';

const InstaReact = ({tag, quantity = 4, cols = 4, links = false, descriptions = false}) => {

    const [posts, setPosts] = React.useState([]);
    const [hashtag, setHashtag] = React.useState(false);

    const link = (url) => {
        if (links) {
            window.open(url, '_blank');
        }
    };

    const getPosts = () => {
        setHashtag(tag[0] === '#');
        const query = hashtag ? `https://www.instagram.com/explore/tags/${tag.replace('#', '')}/?__a=1` : `https://www.instagram.com/${tag}/?__a=1`;
        fetch(query)
            .then(response => {
                if (response.status === 404) {
                    console.error(`${hashtag ? 'Hashtag' : 'Account'} not found for the tag ${tag}`);
                }
                response.json().then(data => {
                    if (data.hasOwnProperty('graphql')) {
                        let new_posts = [];
                        for (let i = 0; i < quantity; i++) {
                            let post = hashtag ? data.graphql.hashtag.edge_hashtag_to_media.edges[i].node : data.graphql.user.edge_owner_to_timeline_media.edges[i].node;
                            if (post) {
                                new_posts.push({
                                    id: post.id,
                                    src: post.display_url,
                                    url: `https://www.instagram.com/p/${post.shortcode}/`,
                                    alt: post.accessibility_caption,
                                    description: post.edge_media_to_caption.edges[0]['node']['text'],
                                });
                            }
                        }
                        setPosts(new_posts);
                    }
                })
            }).catch(error => {
            console.error(`Issue getting Instagram content: ${error}`);
        });
    }

    React.useEffect(() => {
        getPosts();
    }, []);


    return (
        <div style={{display: 'flex', flexWrap: 'wrap', width: '100%'}}>
            {posts.map(post => {
                    return (
                        <div onClick={() => link(post.url)}
                             style={{cursor: links ? 'pointer' : 'default', width: `${100 / cols}%`, display: 'block', padding: '10px', boxSizing: 'border-box', height: 'auto'}}
                             key={post.id}>
                            <img alt={post.alt} src={post.src} style={{width: '100%'}} />
                            {descriptions ? <p style={{ marginTop: '0.5rem', width: '100%', color: '#FFF', fontSize: '0.9rem', mixBlendMode: 'difference'}}>{post.description}</p> : null}
                        </div>
                    )
                }
            )}
        </div>
    )
};

export default InstaReact;